<template>
  <div class="search-box">
    <a-form-model
      layout="inline"
      :model="searchForm"
      @submit="handleSubmit"
      @submit.native.prevent
    >
      <a-form-model-item label="搜索关键字">
        <a-input v-model="searchForm.searchWord" placeholder="输入关键字">
        </a-input>
      </a-form-model-item>
      <a-form-model-item label="商品类目">
        <a-select
          show-search
          placeholder="请选择商品类目"
          style="width: 200px"
          @change="handleChange"
          allowClear
        >
          <a-select-option v-for="c in data" :key="c.id" :value="c.id">
            {{ c.name }}
          </a-select-option>
        </a-select>
      </a-form-model-item>
      <a-form-model-item>
        <a-button type="primary" html-type="submit"> 搜索 </a-button>
      </a-form-model-item>
    </a-form-model>
  </div>
</template>

<script>

export default {
  props: ['data'],
  data() {
    return {
      searchForm: {
        searchWord: '',
        category: '',
      },
    };
  },
  methods: {
    // 提交表单触发
    handleSubmit() {
      this.$emit('searchSubmit', this.searchForm);
    },
    // change类目时触发
    handleChange(value) {
      this.searchForm.category = value;
    },
  },
};
</script>

<style lang='less' scoped>
.search-box {
  padding: 20px 80px;
}
</style>
