<template>
  <div class="product-list">
    <!-- 搜索 -->
    <Search @searchSubmit="searchSubmit" :data="categoryList" />
    <a-button>
      <router-link :to="{ name: 'ProductAdd' }">添加商品</router-link>
    </a-button>
    <!-- 表格 -->
    <ProductTable
      :data="tableData"
      :page="page"
      @change="changePage"
      @edit="editProduct"
      @remove="removeProduct"
    />
  </div>
</template>

<script>
import ProductTable from '@/components/ProductTable.vue';
import Search from '../../components/Search.vue';
import productApi from '../../api/product';
import categoryApi from '../../api/category';

export default {
  components: {
    Search,
    ProductTable,
  },
  data() {
    return {
      tableData: [],
      searchForm: {},
      categoryList: [],
      page: {
        current: 1,
        pageSize: 10,
        showSizeChanger: true,
        total: 1,
      },
      categoryObj: {},
    };
  },
  async created() {
    await categoryApi.list().then((res) => {
      this.categoryList = res.data;
      // 生成一个商品类目对象（this.categoryObj），
      // 以便后续getTableData方法在取得数据后对相应的数据做具体类目映射处理
      res.data.forEach((it) => {
        this.categoryObj[it.id] = it.name;
      });
    });
    this.getTableData();
  },
  methods: {
    searchSubmit(form) {
      this.searchForm = form;
      this.getTableData();
    },
    getTableData() {
      productApi
        .list({
          page: this.current,
          size: this.pageSize,
          ...this.searchForm,
        })
        .then((res) => {
          this.page.total = res.total;
          // 将拿到的所有商品列表数据进一步根据this.categoryObj做类目映射处理
          this.tableData = res.data.map((item) => ({
            ...item,
            categoryName: this.categoryObj[item.category],
          }));
        });
    },
    changePage(page) {
      this.page = page;
      this.getTableData();
    },
    editProduct(record) {
      this.$router.push({
        name: 'ProductEdit',
        params: {
          id: record.id,
        },
      });
    },
    removeProduct(record) {
      const that = this;
      this.$confirm({
        title: '确认删除该商品吗？',
        content: () => <div style="color:red;">{record.title}</div>,
        onOk() {
          productApi.remove({ id: record.id }).then(() => {
            that.getTableData();
          });
        },
        onCancel() {

        },
        class: 'confirm-box',
      });
    },
  },
};
</script>

<style lang="less" scoped>
.product-list {
  position: relative;
  .ant-btn {
    position: absolute;
    right: 20px;
    top: 25px;
  }
}
</style>
